@import url(https://fonts.googleapis.com/css2?family=Ubuntu+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Charmonman&family=Dawning+of+a+New+Day&family=Kristi&family=Marck+Script&family=Monsieur+La+Doulaise&family=Mr+De+Haviland&family=Mrs+Saint+Delafield&family=Nothing+You+Could+Do&display=swap);
body {
  margin: 0;
  padding: 0;
  background: #fc00ff; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #00dbde,
    #fc00ff
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  overflow-x: hidden !important;
}

.App {
  font-family: "Ubuntu Mono", monospace;
  overflow-x: hidden !important;
}
.particles {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -1;
}
::-webkit-scrollbar {
  display: none;
}



.Tilt {
  background: #fc00ff; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #00dbde, #fc00ff);
}
@media (max-width: 600px) {
  .home-logo {
    display: flex;
    justify-content: center;
  }
}

.form {
  background: radial-gradient(
        circle farthest-side at 0% 50%,
        #fb1 23.5%,
        rgba(240, 166, 17, 0) 0
      )
      21px 30px,
    radial-gradient(
        circle farthest-side at 0% 50%,
        #b71 24%,
        rgba(240, 166, 17, 0) 0
      )
      19px 30px,
    linear-gradient(
        #fb1 14%,
        rgba(240, 166, 17, 0) 0,
        rgba(240, 166, 17, 0) 85%,
        #fb1 0
      )
      0 0,
    linear-gradient(
        150deg,
        #fb1 24%,
        #b71 0,
        #b71 26%,
        rgba(240, 166, 17, 0) 0,
        rgba(240, 166, 17, 0) 74%,
        #b71 0,
        #b71 76%,
        #fb1 0
      )
      0 0,
    linear-gradient(
        30deg,
        #fb1 24%,
        #b71 0,
        #b71 26%,
        rgba(240, 166, 17, 0) 0,
        rgba(240, 166, 17, 0) 74%,
        #b71 0,
        #b71 76%,
        #fb1 0
      )
      0 0,
    linear-gradient(90deg, #b71 2%, #fb1 0, #fb1 98%, #b71 0%) 0 0 #fb1;
  background-size: 40px 60px;
}

button {
  border: none;
  outline: none;
}
button:focus {
  outline: none;
}

.form input {
  border: none;
  box-shadow: inset 0 0 10px #000000;
}
input:focus {
  outline: none;
  border: none;
}

@media (max-width: 600px) {
  .form input {
    width: 65%;
    font-size: 18px;
  }
  .form button {
    width: 35%;
    font-size: 18px;
  }
}

.bounding-box {
  position: absolute;
  box-shadow: 0 0 0 3px #149df2 inset;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  cursor: pointer;
}
.fineImage {
  width: 600px;
}
.image-container {
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0);
}
@media (max-width: 600px) {
  .fineImage {
    width: 700px;
  }
}

.main-box {
  display: grid;
  place-items: center;
  height: 100vh;
}
.form-box {
  padding: 40px;
  box-shadow: 4px 8px 8px #aaaaaa;
  border-radius: 50px;
}
.form-logo {
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
}
.form1 label {
  font-size: 19px;
  letter-spacing: 1px;
}
.form1 input {
  border: 1px solid black !important;
  background-color: transparent !important;
  color: white !important;
  font-size: 18px !important;
}
.button {
  border: 1px solid black;
  background: transparent;
  border-radius: 30px;
  padding: 8px 15px;
  transition: background ease-in-out 2s;
  margin-bottom: 10px;
  width: 30%;
}
.button:hover {
  background: #fc00ff; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #00dbde, #fc00ff);
  border-color: white;
}
.button:focus {
  outline: none;
}
.register {
  color: black;
  font-size: 17px;
  letter-spacing: 1px;
}
@media (max-width: 600px) {
  .form-box {
    padding: 10px;
  }
  .register {
    margin-left: 15px;
  }
  .button {
    background: #fc00ff; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #00dbde, #fc00ff);
    border-color: white;
    width: 100%;
  }
}

