*,
*:after,
*:before {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -moz-box-sizing: border-box;
}
.box {
  display: grid;
  place-items: center;
  height: 100vh;
  width: 100%;
  font-family: "Mr De Haviland", cursive;
  margin: 0;
  background: #021841;
  z-index: 100;
}
svg text {
  stroke: #0ed1c1;
  font-size: 80px;
  /* font-weight: 700; */
  stroke-width: 1;
  animation: animate 6s infinite alternate;
}

@keyframes animate {
  0% {
    stroke-dasharray: 0 50%;
    /* stroke-dashoffset: 20%; */
  }
  100% {
    stroke-dasharray: 50% 0;
    /* stroke-dashoffset: -20%; */
  }
}

@media (max-width: 600px) {
  svg text {
    font-size: 200px;
    stroke-width: 1.5;
  }
}

.peoples {
  margin-top: 20px;
  margin-bottom: 10px;
  text-align: center;
  font-size: 22px;
  font-style: italic;
}
span {
  color: red;
  font-size: 30px;
  font-weight: 700;
}

@media (max-width: 600px) {
  .peoples {
    font-size: 20px;
  }
}

